import React, { Component } from 'react';
// import input,{Carousel} from 'react-bootstrap';
import Carousel from 'react-bootstrap/Carousel'
import banner1 from'../images/banner1.jpg';
class HomeSliderComponent extends Component{
    render() {
        return (
        <div>
            <div className="home_slider">
<Carousel variant="dark">
  <Carousel.Item>
    <img className="d-block w-100 img-item"src="images/banner1.jpg"alt="First slide"/>
    
  </Carousel.Item>
  <Carousel.Item>
  <img className="d-block w-100 img-item"src="images/banner12.jpg"alt="First slide"/>
  
  </Carousel.Item>
  <Carousel.Item>
  <img className="d-block w-100 img-item"src="images/banner13.jpg"alt="First slide"/>
   
  </Carousel.Item>
  <div className="fill-form">
                                      <h2>REGISTER NOW</h2>
                      <form className="reg-form">
  
                          <div className="row">
                          <div className="col-md-6">
                           <select className="form-1" name="profile_by">
                                   <option value="">Profile Created By</option>
                                   <option value="Self">Self</option>
                                   <option value="Parents">Parents</option>
                                   <option value="Guardian">Guardian</option>
                                   <option value="Friends">Friends</option>
                                   <option value="Sibling">Sibling</option>
                                    <option value="Relatives">Relatives</option>
                               </select>
                          </div>
                          <div className="col-md-6">
                               <select className="form-2" name="gender">
                                  <option value="">Select Gender</option>
                                  <option value="Female">Female</option>
                                  <option value="Male">Male</option>
                                  </select>
                          </div>						
                          </div>					
                          
                          <div className="row">
                          <div className="col-md-6">
                          <input type="text" placeholder="Enter First Name" name="nickname" id="nickname" />
                          </div>
                          <div className="col-md-6">
                            <input type="text" placeholder="Enter Last Name" name="lastname" id="lastname" />
                          </div>						
                          </div>	
  
                          <div className="row">
                          <div className="col-md-4">
                                                          <select name="day" id="day" className="date1">
                                                                          <option value="01">01</option>
                                                                          <option value="02">02</option>
                                                                          <option value="03">03</option>
                                                                          <option value="04">04</option>
                                                                          <option value="05">05</option>
                                                                          <option value="06">06</option>
                                                                          <option value="07">07</option>
                                                                          
                                                                          
                                                                      </select>
                          </div>
                          <div className="col-md-4">
                                      <select name="month" id="month" className="mothd">
                                                                          <option value="">Month</option>
                                                                          <option value="01">Jan</option>
                                                                          <option value="02">Feb</option>
                                                                          <option value="03">Mar</option>
                                                                          <option value="04">Apr</option>
                                                                          <option value="05">May</option>
                                                                          <option value="06">Jun</option>
                                                                          <option value="07">Jul</option>
                                                                          <option value="08">Aug</option>
                                                                          <option value="09">Sep</option>
                                                                          <option value="10">Oct</option>
                                                                          <option value="11">Nov</option>
                                                                          <option value="12">Dec</option>
                                                                      </select>
                          </div>	
                          <div className="col-md-4">
                           <select name="year" id="year" className="years">
                                   <option value="">Year</option>
                                   <option value="2010">2010</option>
                                   <option value="2011">2011</option>
                                   <option value="2012">2012</option>
                                   <option value="2013">2013</option>
                                   <option value="2014">2014</option>
                                   </select>
                          </div>							
                          </div>							
                          
                          <div className="row">
                          <div className="col-md-6">
                              <select name="reg" id="regi" className="region">
                                   <option value="">Select Your Religion</option>
                                   <option value="2010">Religion</option>
                                   <option value="2011">Religion</option>
                                   <option value="2012">Religion</option>
                                   <option value="2013">Religion</option>
                                   <option value="2014">Religion</option>
                              </select>
                          </div>
                          <div className="col-md-6">
                              <select name="reg1" id="regi1" className="region1">
                                   <option value="">Select Religion First</option>
                                   <option value="Select Religion First">Select Religion First</option>
                              </select>
                          </div>						
                          </div>		
  
                          <div className="row">
                          <div className="col-md-6">
                              <select name="mother-tung" id="mother-tung1" className="mother">
                                   <option value="">Mother Tongue</option>
                                   <option value="2010">Mother Tongue</option>
                              </select>
                          </div>
                          <div className="col-md-6">
                              <select name="Country" id="Country1" className="Country">
                                   <option value="">Country</option>
                                   <option value="India">India</option>
                              </select>
                          </div>						
                          </div>		
  
                          <div className="row">
                          <div className="col-md-6">
                          <select className="country-code" name="code" id="code">
                                  <option value="+93">+93</option>
                                  <option value="+355">+355</option>
                                   <option value="+213">+213</option>
                          </select>
                          </div>
                          <div className="col-md-6">
                              <input type="number" placeholder="Enter Your 10 Digit No" name="mobile" id="mobile" />
                          </div>						
                          </div>		
                          <div className="row">
                          <div className="col-md-12">
                          <input type="email" placeholder="Enter Your Email Id" name="email" />
                          </div>						
                          </div>
  
                          <div className="row">
                          <div className="col-md-12">
                              <div className="code-accept">
                              <input type="checkbox" id="terms" name="chk_terms" data-validetta="required" />
                              <span>I accept <a href="#" target="_blank">terms &amp; conditions</a> and <a href="#" target="_blank">privacy policy</a></span>
                              </div>						
                          </div>						
                          </div>
                          
                          <div className="row">
                          <div className="col-md-12">
                              <div className="submit-btn">
                              <button type="submit" name="reg_sub">Register Now</button>
                              </div>						
                          </div>						
                          </div>
                          
                          
                          
                      </form>
                  </div>

</Carousel>
        </div>
  
        </div>)
        
    }

     
      
}
export default HomeSliderComponent;